.f_cont_wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
}

.f_cont_wrap {
  position: absolute;
  width: 60%;
  min-height: 100%;
  right: 0;
  top: 0;
  z-index: 2;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
}
.f_cont_wrap canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.f_right_section {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.f_right_s_wrtxt ul {
  color: #ffff;
  list-style: none;
}
.f_right_s_wrtxt ul li {
  font-weight: bold;
  width: 100%;
  color: #e9e3c7;
  text-transform: uppercase;
  margin: 10px;
  position: relative;
}
.f_right_s_wrtxt ul .f_fsec_ul_bigtxt {
  font-size: 70px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}
.f_right_s_wrtxt ul :nth-child(2) {
  color: #dd5a12;
}
.f_right_s_wrtxt ul .f_fsec_ul_expmtxt {
  color: #dd5a12;
}

.f_ul_bigsc {
  display: block;
}
.f_ul_bigsc .ful_bgsec_arr {
  width: 20px;
  height: 45px;
  margin-top: 40px;
  display: block;
  background: url("../media/f_sec/down-arrow.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  opacity: 0.8;
}

.f_ul_smsc {
  display: none;
}
.f_ul_smsc .ful_bgsec_arr {
  width: 20px;
  height: 45px;
  margin-top: 40px;
  display: block;
  background: url("../media/f_sec/down-arrow.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  opacity: 0.8;
}

@media screen and (max-width: 1255px) {
  .f_right_s_wrtxt ul .f_fsec_ul_bigtxt {
    font-size: 55px;
  }
}
@media screen and (max-width: 1000px) {
  .f_cont_wrap {
    z-index: 1;
  }

  .f_right_section {
    z-index: 2;
  }
}
@media screen and (min-width: 500.5px) and (max-width: 550px) {
  .f_right_s_wrtxt ul .f_fsec_ul_bigtxt {
    font-size: 50px;
  }
}
@media screen and (max-width: 500px) {
  .f_ul_bigsc {
    display: none;
  }

  .f_ul_smsc {
    display: block;
  }
  .f_ul_smsc .ful_bgsec_arr {
    height: 30px;
    margin-top: 30px;
  }

  .f_right_s_wrtxt .f_ul_smsc :nth-child(1) {
    font-size: 60px;
  }
  .f_right_s_wrtxt .f_ul_smsc :nth-child(2) {
    font-size: 20px;
  }
  .f_right_s_wrtxt .f_ul_smsc :nth-child(3) {
    font-size: 30px;
  }

  .f_cont_wrap {
    z-index: 1;
    margin-top: 120px;
  }

  .f_right_section {
    z-index: 2;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    padding-top: 100px;
  }
}
@media screen and (max-width: 600px) {
  .f_right_s_wrtxt ul {
    padding: 0;
  }
}